.segment{
  border: 1px solid rgb(56,126,245);
  width:14rem;
  box-sizing: content-box;
  margin:0 auto;
  margin-top:10px;
  height: 30px;
  border-radius: 5px;
  display: block;
  li{
    display: inline-block;
    width:7rem;
    height:30px;
    text-align: center;
    line-height: 30px;
    &:nth-of-type(1){
      float: left;
    }
  }
  .active{
    background: rgb(56,126,245);
    color:white;
  }

}
