.coupon{
    .row .qr {
    font-weight: 600;
    width:220px;
    position: relative;
    min-height:270px;
    // background: rgb(232,40,89);
    background: rgb(240,157,1);
   margin: 5px auto;
    padding-top:3px;
    text-align: center;
    // color:white;
    color:rgb(50,50,50);

    canvas{
        position: absolute;
        left:50%;
        top:30px;
        transform: translateX(-50%);
    }
    .bottomInfo{
        position: absolute;
        bottom:0px;
        min-height:50px;
        width:100%;
        background: rgb(245,221,61);
        padding:0px;
        left:0;
        p{
            margin: 0;
            padding: 0;
            font-weight: 600;
            &:nth-of-type(1){
                font-size: 18px;

            }
            &:nth-of-type(2){
                font-weight: 600;
            }

             &.timeText{
                font-size: 12px;
                // white-space:nowrap;
                font-weight: 600;

            }
        }
    }
}
}
