.sideBar{

  .topInfo{
    height:150px;
    width:100%;
    position: relative;
    background: url('../images/main_bg.jpg');
    background-size: 100% 150px;
    position: relative;
    div{
      color:white;
      position: absolute;
      top:50%;
      transform: translateY(-50%);
      // bottom:0;
      margin: 0;
      padding: 0;
      left:120px;
      font-weight: 700;
    }
  }

  .avatar{
    height:80px;
    width: 80px;
    border-radius: 50%;
    position: absolute;
    top:50%;
    left:20px;
    transform:  translateY(-50%);
  }
}
