.orderDetail2{
    .coupon{
        width:80%;
        margin: 10px auto;
        height:100px;
        background: rgb(224,143,5);
        position: relative;
        text-align: center;
        p{
            margin:0;
            
        }
        .topInfo{
            p{
                &:nth-of-type(1){
                    font-size:20px;
                    font-weight: 700;
                }
                &:nth-of-type(2){
                    margin-top:5px;
                }
            }
            
        }
        .bottomInfo{
            background: rgb(0,2,0);
            width:100%;
            height:35px;
            line-height:35px;
            font-weight:700;
            position: absolute;
            left:0;
            bottom:0;
            color:rgb(224,143,5);
        }
    }
}